.navbar {
  display: flex;
  justify-content: space-between;
  align-items: center;
  background-color: #000;
  padding: 20px 50px;
}

.logo {
  font-size: 1.8rem;
  color: #e50914;
  font-weight: bold;
}

.nav-links {
  list-style: none;
  display: flex;
  gap: 25px;
}

.nav-links a {
  text-decoration: none;
  color: #fff;
  font-size: 1rem;
  font-weight: bold;
}

.nav-links a:hover {
  color: #e50914;
}
