.home-container {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 50px;
  background-color: #000;
  color: #fff;
  min-height: 90vh;
  position: relative;
  overflow: hidden;
}

.content {
  flex: 1;
  z-index: 2;
}

.content h1 {
  font-size: 3.5rem;
  margin-bottom: 10px;
}

.content h2 {
  font-size: 2.5rem;
  color: #e50914;
  margin-bottom: 20px;
}

.content p {
  font-size: 1.2rem;
  max-width: 500px;
  margin-bottom: 30px;
}

.highlight {
  font-family: 'Cursive', sans-serif;
}

.social-icons {
  font-size: 1.8rem;
  display: flex;
  gap: 15px;
  margin-bottom: 30px;
}

.buttons {
  display: flex;
  gap: 20px;
}

.btn {
  padding: 12px 30px;
  font-size: 1rem;
  border: none;
  cursor: pointer;
  border-radius: 8px;
  transition: all 0.3s;
}

.hire {
  background-color: #e50914;
  color: #fff;
}

.hire:hover {
  background-color: #b00610;
}

.resume {
  background-color: transparent;
  border: 2px solid #e50914;
  color: #e50914;
}

.resume:hover {
  background-color: #e50914;
  color: #fff;
}

.image-container {
  flex: 1;
  position: relative;
  z-index: 1;
  display: flex;
  justify-content: center;
  align-items: center;
}

.circle-bg {
  position: absolute;
  width: 400px;
  height: 400px;
  border-radius: 50%;
  background: #e50914;
  z-index: -1;
}

.image-container img {
  width: 350px;
  border-radius: 10px;
  object-fit: cover;
}
